.time {
  max-width: 600px;

  padding: 10px;
  margin: 0 auto;

  background-color: #f2f2f2;

  &__title {
    margin-bottom: 12px;

    text-transform: uppercase;
    font-size: 20px;
    color: #011638;
  }

  &__chart {
    margin-bottom: 20px;
  }

  &__item {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;

    margin-bottom: 14px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__name {
    max-width: 100px;
    min-width: 100px;
    margin-right: 10px;
    margin-bottom: 4px;

    text-align: center;
    color: #364156;
  }

  &__data {
    position: relative;

    width: 400px;
    height: 30px;
    background-color: #9fa8b9;
  }

  &__data-fill {
    position: absolute;

    display: flex;
    justify-content: center;
    align-items: center;

    height: inherit;

    background-color: #6be6f8;
  }

  &__data-value {
    color: #000;
    font-size: 16px;
  }

  &__button {
    width: 100%;
    height: 30px;

    background-color: #d81159;
    border-radius: 4px;
    font-size: 16px;
    color: white;

    cursor: pointer;

    &:hover {
      background-color: #b90e4b;
    }

    &:active {
      background-color: #960f40;
    }
  }
}
